body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: rgb(0, 3, 46);
}
/*
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("https://firebasestorage.googleapis.com/v0/b/dear-diary-hi.appspot.com/o/bgimages%2Fice.jpeg?alt=media&token=1d94d14f-74f2-413e-9741-40579129faf1");
  background-repeat: no-repeat;
  background-size: 100vw 100vh;
*/
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;

  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.textSmallFade {
  font-size: 14px;
  color: cadetblue;
}
.testCard {
  width: 90vw;
  min-width: 400px;
}
.testName {
  display: inline-block;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
}
.testStartBtn {
  float: right;
}

.bgGym {
  background-image: linear-gradient(45deg, rgba(0, 0, 0, 0.245), black),
    url(/static/media/bg.968ef6e9.jpeg);
  background-size: cover;
  background-position: -200px 0px;
  width: 100%;
  overflow-x: hidden;
  height: 100vh;
}
.centerToDisplay {
  position: absolute;
  top: 40%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}

.FlexGrid {
  display: flex;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  position: relative;
}
.Welcome {
  display: block;
  height: -webkit-fit-content;
  height: fit-content;
  background-color: rgba(245, 245, 220, 0.177);
  padding: 50px;
  color: aliceblue;
  z-index: 1;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.Animation,
.AnimationR {
  display: block;
  position: absolute;
  top: 0px;
  font-size: 500px;
  left: 0px;
  color: white;
  z-index: -1;
  animation-name: welc;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}
@keyframes welc {
  0% {
    top: 0px;
    left: 50px;
  }
  50% {
    top: 1000px;
    left: 50px;
  }
  100% {
    top: 0px;
    left: 50px;
  }
}
.WelcText {
  opacity: 1;
  filter: blur(0px);
}

.AnimationR {
  transform: rotate(180deg);
  animation-name: welcR;
}

@keyframes welcR {
  0% {
    bottom: 0px;
    right: 50px;
  }
  50% {
    bottom: 1000px;
    left: 50px;
  }
  100% {
    bottom: 0px;
    right: 50px;
  }
}
.float-right {
  float: right;
}

.navBar {
  display: flex;

  margin: auto;
  padding-top: 20px;
}
.navBarItem {
  margin: auto;
  border: 1px solid rgba(245, 245, 245, 0.449);
  border-radius: 12px;
  padding: 5px;
  transition-duration: 0.5s;
  text-decoration: none;
  color: aliceblue;
}
.navBarItem:hover {
  background-color: rgb(11, 97, 255);
  text-decoration: none;
  color: aliceblue;
}

